<template>

    <div>

        <!--Design de Mobile-->
        <v-container class="hidden-lg-and-up">
            <v-container grid-list-lg>
                    <v-layout row wrap>
                        <v-flex xs12 sm12 md6 offset-md3>
                            <v-card>

                                <!--Cabecalho da pagina-->
                                <v-toolbar :extended="false"  class="white explicador-bar-fixed">
                                    <!--<v-toolbar-side-icon></v-toolbar-side-icon>-->

                                    <!--Nome do utilizador-->
                                    <span class="headline">{{ nomeUser }}</span>

                                    <v-spacer></v-spacer>

                                    <!--Menu dropdown para as outras funcionalidades da pagina-->
                                    <v-menu v-if="true" offset-y xs3>
                                        <v-btn icon slot="activator">
                                            <v-icon>more_vert</v-icon>
                                        </v-btn>
                                        <v-list>
                                            <div>

                                                <v-list-tile @click="scrollTo(520)">
                                                    <v-list-tile-action>
                                                        <v-icon>library_books</v-icon>
                                                    </v-list-tile-action>
                                                    <v-list-tile-title>Disciplinas</v-list-tile-title>
                                                </v-list-tile>


                                                <v-list-tile @click="scrollTo(0)">
                                                    <v-list-tile-action>
                                                        <v-icon>phone</v-icon>
                                                    </v-list-tile-action>
                                                    <v-list-tile-title>Contacto</v-list-tile-title>
                                                </v-list-tile>

                                                <v-list-tile @click="scrollTo(213)">
                                                    <v-list-tile-action>
                                                        <v-icon>perm_contact_calendar</v-icon>
                                                    </v-list-tile-action>
                                                    <v-list-tile-title>Info. Pessoais </v-list-tile-title>
                                                </v-list-tile>

                                                <v-list-tile @click="scrollTo(1000)">
                                                    <v-list-tile-action>
                                                        <v-icon>school</v-icon>
                                                    </v-list-tile-action>
                                                    <v-list-tile-title>Inf. Academicas </v-list-tile-title>
                                                </v-list-tile>

                                                <v-list-tile @click="scrollTo(10000)">
                                                    <v-list-tile-action>
                                                        <v-icon>location_on</v-icon>
                                                    </v-list-tile-action>
                                                    <v-list-tile-title>Localizacao </v-list-tile-title>
                                                </v-list-tile>

                                            </div>
                                        </v-list>
                                    </v-menu>


                                </v-toolbar>

                                <!--Corpo-->
                                <v-card-text class="grey lighten-5 text-xs-center">

                                    <!--Dados Pessoais-->
                                    <v-layout  style="margin-top: 45px" justify-center>
                                        <v-flex xs12 sm8 md12>

                                            <v-card>

                                                <!--Imagem do user-->
                                                <v-card-media :src="img" height="200px">
                                                </v-card-media>

                                                <!--Nome do user-->
                                                <v-card-title class="cyan darken-1">
                                                    <span class="headline white--text">{{ nomeUser }}</span>
                                                </v-card-title>


                                                <v-list two-line>

                                                    <!--Telfone do user-->
                                                    <v-list-tile @click="">
                                                        <v-list-tile-action>
                                                            <v-icon>phone</v-icon>
                                                        </v-list-tile-action>
                                                        <v-list-tile-content>
                                                            <v-list-tile-title>{{ isUser && user.telfone !== '' && user.telfone !== null ? user.telfone : "84 " }}</v-list-tile-title>
                                                        </v-list-tile-content>
                                                        <v-list-tile-action>
                                                            <v-icon>chat</v-icon>
                                                        </v-list-tile-action>
                                                    </v-list-tile>

                                                    <v-divider inset></v-divider>

                                                    <!--Email do user-->
                                                    <v-list-tile @click="">
                                                        <v-list-tile-action>
                                                            <v-icon>mail</v-icon>
                                                        </v-list-tile-action>
                                                        <v-list-tile-content>
                                                            <v-list-tile-title>{{ isUser ? user.email : 'Sem Email' }}</v-list-tile-title>
                                                        </v-list-tile-content>
                                                    </v-list-tile>

                                                    <v-divider inset></v-divider>

                                                    <!--Sexo do user-->
                                                    <v-list-tile @click="">
                                                        <v-list-tile-action>
                                                            <v-icon>perm_identity</v-icon>
                                                        </v-list-tile-action>
                                                        <v-list-tile-content>
                                                            <v-list-tile-title>{{ gender }}</v-list-tile-title>
                                                        </v-list-tile-content>
                                                    </v-list-tile>

                                                </v-list>

                                            </v-card>
                                        </v-flex>
                                    </v-layout>

                                    <!--Disciplinas-->
                                    <v-layout justify-center>
                                        <v-flex xs12 sm8 md12>

                                            <v-card>
                                                <v-card-title class="green darken-1">
                                                    <span class="headline white--text">Disciplinas</span>
                                                </v-card-title>

                                                <v-list two-line style="height: 400px">


                                                    <template v-for="(subject, i) in userSubjects">
                                                        <v-list-tile @click="">
                                                            <v-list-tile-action>
                                                                <v-icon>library_books</v-icon>
                                                            </v-list-tile-action>
                                                            <v-list-tile-content>
                                                                <v-list-tile-title>{{ subject.name }}</v-list-tile-title>
                                                            </v-list-tile-content>
                                                            <!--<v-list-tile-action>-->
                                                            <!--<v-icon>more_vert</v-icon>-->
                                                            <!--</v-list-tile-action>-->
                                                        </v-list-tile>

                                                        <v-divider inset v-if="i !== userSubjects.length -1"></v-divider>
                                                    </template>

                                                    <span v-if="!hasSubjects" class="red--text">Sem disciplinas</span>

                                                </v-list>
                                            </v-card>
                                        </v-flex>
                                    </v-layout>

                                    <!--Dados Academicos-->
                                    <v-layout justify-center>
                                        <v-flex xs12 sm8 md12>

                                            <v-card>
                                                <v-card-title class="orange darken-1">
                                                    <span class="headline white--text">Dados Academicos</span>
                                                </v-card-title>

                                                <v-list two-line>
                                                    <v-list-tile @click="">
                                                        <v-list-tile-action>
                                                            <v-icon>school</v-icon>
                                                        </v-list-tile-action>
                                                        <v-list-tile-content>
                                                            <v-list-tile-title>{{ userInstitution }}</v-list-tile-title>
                                                        </v-list-tile-content>
                                                        <!--<v-list-tile-action>-->
                                                            <!--<v-icon>chat</v-icon>-->
                                                        <!--</v-list-tile-action>-->
                                                    </v-list-tile>
                                                    <v-divider inset></v-divider>
                                                    <v-list-tile @click="">
                                                        <v-list-tile-action>
                                                            <v-icon>school</v-icon>
                                                        </v-list-tile-action>
                                                        <v-list-tile-content>
                                                            <v-list-tile-title>{{ userFaculty }}</v-list-tile-title>
                                                        </v-list-tile-content>
                                                        <!--<v-list-tile-action>-->
                                                            <!--<v-icon>chat</v-icon>-->
                                                        <!--</v-list-tile-action>-->
                                                    </v-list-tile>

                                                    <v-divider inset></v-divider>


                                                    <v-list-tile @click="">
                                                        <v-list-tile-action>
                                                            <v-icon>school</v-icon>
                                                        </v-list-tile-action>
                                                        <v-list-tile-content>
                                                            <v-list-tile-title>{{ userCourse }}</v-list-tile-title>
                                                        </v-list-tile-content>
                                                    </v-list-tile>

                                                    <v-divider inset></v-divider>

                                                    <v-list-tile @click="">
                                                        <v-list-tile-action>
                                                            <v-icon>school</v-icon>
                                                        </v-list-tile-action>
                                                        <v-list-tile-content>
                                                            <v-list-tile-title>{{ userDepartment }}</v-list-tile-title>
                                                        </v-list-tile-content>
                                                    </v-list-tile>
                                                </v-list>
                                            </v-card>
                                        </v-flex>
                                    </v-layout>

                                    <!--Localizacao-->
                                    <v-layout justify-center>
                                        <v-flex xs12 sm8 md12>

                                            <v-card>
                                                <v-card-title class="blue darken-1">
                                                    <span class="headline white--text">Localizacao</span>
                                                </v-card-title>

                                                <v-list two-line>
                                                    <v-list-tile @click="">
                                                        <v-list-tile-action>
                                                            <v-icon>location_on</v-icon>
                                                        </v-list-tile-action>
                                                        <v-list-tile-content>
                                                            <v-list-tile-title>{{ userProvince }}</v-list-tile-title>
                                                        </v-list-tile-content>
                                                        <!--<v-list-tile-action>-->
                                                            <!--<v-icon>chat</v-icon>-->
                                                        <!--</v-list-tile-action>-->
                                                    </v-list-tile>
                                                    <v-divider inset></v-divider>
                                                    <v-list-tile @click="">
                                                        <v-list-tile-action>
                                                            <v-icon>location_on</v-icon>
                                                        </v-list-tile-action>
                                                        <v-list-tile-content>
                                                            <v-list-tile-title>{{ userCity }}</v-list-tile-title>
                                                        </v-list-tile-content>
                                                        <!--<v-list-tile-action>-->
                                                            <!--<v-icon>chat</v-icon>-->
                                                        <!--</v-list-tile-action>-->
                                                    </v-list-tile>

                                                    <v-divider inset></v-divider>

                                                    <v-list-tile @click="">
                                                        <v-list-tile-action>
                                                            <v-icon>location_on</v-icon>
                                                        </v-list-tile-action>
                                                        <v-list-tile-content>
                                                            <v-list-tile-title>{{ userBairro }}</v-list-tile-title>
                                                        </v-list-tile-content>
                                                    </v-list-tile>


                                                </v-list>
                                            </v-card>
                                        </v-flex>
                                    </v-layout>

                                </v-card-text>
                            </v-card>
                        </v-flex>
                    </v-layout>
                </v-container>


            </v-container>

        <!--Design para computadores e outros-->
        <v-container fluid class="pt-1 hidden-md-and-down">

            <v-layout>

                <!--Nav-bar fixo esquerdo-->
                <v-flex xs2 offset-xs2 class="hidden-xs-only">
                    <v-layout class="explicador-bar-fixed">
                        <v-flex xs2 offset-xs2>
                            <v-layout>
                                <!--Apresentacao do explicador aqui.-->
                                <v-flex xs12 class="white pa-2 elevation-5 mb-3 pb-4">

                                    <v-layout>
                                        <!--Foto-->
                                        <v-flex xs3>
                                            <!--Foto de utilizador-->
                                            <img :src="img" class="img-radius"/>

                                        </v-flex>

                                        <!--Nome do explicador-->
                                        <v-flex xs9 class="ml-2 orange--text">

                                            <!--Nome do utilizador-->
                                            <h2>{{ nomeUser }}</h2>

                                        </v-flex>

                                    </v-layout>

                                    <v-divider class="mt-2"></v-divider>

                                    <v-layout>

                                        <v-flex class="mt-3">
                                            <!--Instituicao do utilizador-->
                                            <h4 class="pb-2 blue--text"><v-icon>school</v-icon> {{ userInstitution }}</h4>
                                            <h4 class="pb-2 grey--text"><v-icon>school</v-icon> {{ userCourse }}</h4>
                                            <h4 class="pb-2 green--text"><v-icon>school</v-icon> {{ userDepartment }}</h4>

                                        </v-flex>

                                    </v-layout>
                                </v-flex>

                            </v-layout>
                            <v-layout>
                                <v-flex>

                                    <v-btn
                                            @click="scrollTo(460)"
                                            block=""
                                            title="Ver os contactos do explicador">
                                        <v-icon left dark>phone</v-icon>
                                         Ver Contactos
                                    </v-btn>

                                    <v-btn block color="white" router @click="scrollTo(0)" class="azul-principal--text">Disciplinas</v-btn>

                                    <v-btn block color="white" router @click="scrollTo(460)" class="azul-principal--text">Informaes pessoais</v-btn>

                                    <v-btn block color="white" router @click="scrollTo(910)" class="azul-principal--text">Informaes Acadmicas</v-btn>

                                    <v-btn block color="white" router @click="scrollTo(1360)" class="azul-principal--text">Localizacao</v-btn>

                                </v-flex>
                            </v-layout>

                            <!--<v-btn round outline block router >Testemunhos</v-btn>-->

                        </v-flex>
                    </v-layout>
                </v-flex>

                <!--Informacao central nao fixa-->
                <v-flex  xs12 sm6 offset-sm1 class="ml-2" >

                    <v-container class="white">
                        <v-layout>
                            <v-flex xs12>

                                <!--Seccao das disciplinas-->
                                <v-layout id="disciplinas" class="elevation-2" style="height: 450px">

                                    <explicador-subject-info :user="user"></explicador-subject-info>

                                </v-layout>

                                <!--Seccao dos dados pessoais-->
                                <v-layout id="dadosPessoais" class="elevation-2" style="height: 450px">

                                    <explicador-personal-info :user="user"></explicador-personal-info>

                                </v-layout>

                                <!--Sessao de Dados Academicos-->
                                <v-layout id="dadosAcademicos" class="elevation-2" style="height: 450px">

                                    <explicador-academic-info :user="user"></explicador-academic-info>

                                </v-layout>

                                <!--Sessao de Dados de Localizacao-->
                                <v-layout id="localizacao" class="elevation-2" style="height: 450px">

                                    <explicador-location-info :user="user"></explicador-location-info>

                                </v-layout>


                            </v-flex>
                        </v-layout>
                    </v-container>
                </v-flex>



            </v-layout>


        </v-container>

    </div>

</template>

<script>
    export default {
        props: ['user', 'name'],
        data: () => ({
            hidden: false,
            nameU: ''
        }),
        created () {
        },
        computed: {
            options () { //usado para fazer o scroll
                return {
                    duration: 1000,
                    offset: 0,
                    easing: 'easeInQuint'
                }
            },
            isUser () {
                return this.user !== undefined && this.user !== null
            },
            img () {
                return this.isUser ? this.user.photoURL : '//ssl.gstatic.com/s2/oz/images/sge/grey_silhouette.png'
            },
            nomeUser() {
                return this.isUser && this.user.name !== null && this.user.name !== '' ? this.user.name : 'Sem Nome'
            },
            gender () {

                const negative = 'Sexo nao definido'

                if (!this.isUser) return negative

                let genero = this.user.gender

                return genero && genero !== null && genero && genero !== '' ? genero : negative


            },
            userInstitution () {

                if (!this.isUser) return 'Sem Universidade (Escola)'

                let inst = this.user.Educations.institution
                return inst !== null && inst !== '' ? inst : 'Sem Universidade (Escola)'
            },
            userFaculty () {

                const negative = 'Sem Faculdade'

                if (!this.isUser) return negative

                let fac = this.user.Educations.faculty
                return this.isUser && fac !== null && fac !== undefined && fac !== '' ? 'Faculdade de ' + fac : negative
            },
            userCourse () {

                const negative = 'Sem Curso'

                if (!this.isUser) return negative

                let course = this.user.Educations.course

                return course && course !== null && course !== undefined && course !== '' ? 'Curso de ' + course : negative

            },
            userDepartment () {

                const negative = 'Sem Departamento'

                if (!this.isUser) return negative

                let department = this.user.Educations.department

                return department && department !== null && department !== undefined && department !== '' ? department : negative


            },
            userNivel () {

                const negative = 'Sem Nivel'

                if (!this.isUser) return negative

                let nivel = this.user.Educations.nivel

                return nivel && nivel !== null && nivel !== undefined && nivel !== '' ? nivel : negative


            },
            hasSubjects () {

                if ( !this.isUser ) return false

                let subjects = this.user.Educations.subjects

                return subjects.length > 0
            },

            subjectsTitle () {

                if (!this.hasSubjects) return 'Ainda no registou as disciplinas que explica'

                return 'Disciplinas do ' + this.nomeUser
            },
            userSubjects () {

                const negative = []

                if (!this.isUser) return negative

                let subjects = this.user.Educations.subjects

                return subjects


            },

            hasSomeLocation () {

                if ( !this.isUser ) return false

                return this.user.Location

            },

            userProvince () {

                const negative = 'Provincia No registada'

                if (!this.hasSomeLocation) return negative

                let userProv = this.user.Location.province

                return userProv !== null && userProv !== undefined && userProv !== '' ? userProv : negative

            },

            userCity () {

                const negative = 'Cidade No registada'

                if (!this.hasSomeLocation) return negative

                let userCity = this.user.Location.city

                return userCity !== null && userCity !== undefined && userCity !== '' ? userCity : negative

            },

            userBairro () {

                const negative = 'Bairro No registado'

                if (!this.hasSomeLocation) return negative

                let userBairro = this.user.Location.bairro

                return userBairro !== null && userBairro !== undefined && userBairro !== '' ? userBairro : negative

            },

        },
        methods: {

            scrollTo (top) {

                window.scroll({
                    top: top,
                    left: 10,
                    behavior: 'smooth'
                });

                // Scroll to a certain element
//                document.querySelector(el).scrollIntoView({
//                    behavior: 'smooth'
//                });

            },
            click () {
                alert('cliou')
            }

        },
    }
</script>
<style scoped>
    .explicador-bar-fixed {
        z-index: 0;
    }
    .explicador-edit-item {
        float: right;
        cursor: pointer
    }
    .icon {
        font-size: 18px;
    }
    .img-radius {
        width: 50px;
        height: 50px!important;
    }
    .container {
        padding: 0px;
    }


</style>


